import React, { useState } from "react";
import CreateSite from "../../components/Sites/CreateSite";
import { Button } from "react-bootstrap";
import UiModal from "../../components/UI/UiModal";
import DTable from "../../components/DTable";

function Site() {
  // state and reducers
  const [showSiteForm, SetShowSiteForm] = useState(false);

  // to refresh table
  const [refreshTable, setRefreshTable] = useState(false);
  const hideModal = () => SetShowSiteForm(false);

  const columns = [
    {
      name: "Parent Company",
      selector: (row) => row.parent_company,
      sortable: true,
    },
    {
      name: "Site Name",
      selector: (row) => row.site_name,
    },
    {
      name: "Type of Owner",
      selector: (row) => row.type_of_owner,
    },
    {
      name: "Owner Name",
      selector: (row) => row.owner_name,
    },
    {
      name: "Company",
      selector: (row) => row.company.name,
    },
  ];

  const DTableFunction = function (data) {
    return data;
  };
  return (
    <>
      <Button
        className="mb-3"
        onClick={() => SetShowSiteForm((state) => !state)}
      >
        Add Site
      </Button>
      {showSiteForm ? (
        <UiModal
          showStatus={showSiteForm}
          setModalStatus={hideModal}
          showHeader={true}
          title="Add Site"
          body={
            <CreateSite hideModal={hideModal} refreshTable={setRefreshTable} />
          }
        />
      ) : (
        ""
      )}
      <DTable
        url="site/?ordering=-date_created"
        transformFunction={DTableFunction}
        columns={columns}
        refreshTable={refreshTable}
        setRefreshTable={setRefreshTable}
      />
    </>
  );
}

export default Site;
