import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getFromLocalStorage,
  setToLocalStorage,
} from "../helpers/helperFunctions";
import { authAction } from "../store/authStore";
import { useNavigate } from "react-router-dom";
import { uiAction } from "../store/uiStore";
import useUiAction from "./useUiAction";
import useAuthAction from "./useAuthAction";

// if isAuth needed then it will be added by useFetch just make it true
const initialSendDataState = {
  url: "",
  fetchObj: "",
  notificationTime: 8,
  timeOut: 20,
  errorMsg: "",
  errTitle: "",
  expectStatusCode: [200, 201, 400],
  isAuthNeeded: false,
};

function useFetch() {
  // states and reducer
  const [status, setStatus] = useState({ isLoading: false, isError: false });

  //   notification time : show usr msg if taking then usual, timeOut mean : cut the connection and show user msg server down or internet connection slow...
  const [sendData, setSendData] = useState(initialSendDataState);
  const [response, setResponse] = useState(null);

  // ref to make sure only 1 time refresh token fires not more then once.
  // 0 : you can fire 1 mean you can't fire
  const refreshStatus = useRef(0);
  //   custom hooks
  const uiDispatcher = useUiAction();
  const authDispatcher = useAuthAction();
  // react-router
  const navigate = useNavigate();

  // redux
  const authData = useSelector((state) => state.authStore);

  // function starts
  const fetchReq = async function (url, fetchObj) {
    const request = await fetch(
      `http://aumhealthresort.com/powercrmlatest/api/${url}`,
      fetchObj
    );
    let response = null;
    if (request.ok) {
      response = await request.json();
    }
    return { status: request.status, data: response, isError: !request.ok };
  };
  const refreshAuthToken = async function () {
    if (!authData.refreshToken) {
      return;
    }
    // const localData = getFromLocalStorage("loginInfo", true);
    if (authData.logInOperation === 0 || authData.logInOperation === 1) {
      // mean already refresh token thing started or token refreshed
      return;
    }
    authDispatcher({
      type: "setAuthStatus",
      data: {
        ...authData,
        logInOperation: 1,
      },
    });
    const response = await fetchReq("token/refresh/", {
      headers: {
        // "Access-Control-Allow-Origin": "*",
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON.stringify({
        refresh: authData.refreshToken,
      }),
    });
    if (response.status === 200) {
      const authDataObj = {
        ...authData,
        accessToken: response.data.access,
        timeOfLogin: Date.now(),
        logInOperation: 1,
        loggedIn: true,
      };
      authDispatcher({
        type: "setAuthStatus",
        data: authData,
      });

      setToLocalStorage("loginInfo", authDataObj, true);
    } else {
      navigate("/login");
    }
  };

  const ajaxRequest = async function () {
    try {
      setStatus({ isLoading: true, isError: false });

      const id = setTimeout(() => {
        uiDispatcher({
          type: "setNotification",
          data: {
            show: true,
            heading: "",
            msg: `Taking Longer Then Expected...`,
          },
        });
      }, sendData.notificationTime * 1000);

      const metaInfo = sendData.fetchObj;
      if (sendData.isAuthNeeded) {
        metaInfo.headers = {
          ...metaInfo.headers,
          Authorization: `Bearer ${authData.accessToken}`,
        };
      }
      const response = await fetchReq(sendData.url, metaInfo);

      clearTimeout(id);
      if (response.status === 401) {
        // authorize usr again
        authDispatcher({
          type: "setAuthStatus",
          data: {
            ...authData,
            logInOperation: -1,
          },
        });

        return;
      }

      if (sendData.expectStatusCode.includes(response.status)) {
        setSendData(initialSendDataState);
        setResponse({
          status: response.status,
          isError: !response.ok,
          isNetwork: false,
          data: response.data,
        });
      } else {
        setStatus({ isLoading: false, isError: true });
        setResponse({
          status: response.status,
          isError: true,
          isNetwork: false,
          data: response.data,
        });
      }
      setStatus({ isLoading: false, isError: false });
    } catch (e) {
      setStatus({ isLoading: false, isError: true });
      //   catch will be later
      return {
        status: null,
        isError: true,
        isNetwork: true,
        data: null,
        error: e,
      };
    }
  };

  useEffect(() => {
    console.log("now it is", authData.logInOperation);
    // 0 means trying to refresh token
    if (
      !response &&
      !status.isLoading &&
      !status.isError &&
      sendData.url?.length
    ) {
      ajaxRequest();
    }
    // -1 mean doing nothing and 1 mean all set
    if (authData.logInOperation === -1 && !refreshStatus.current) {
      refreshStatus.current = 1;
      authDispatcher({
        type: "setAuthStatus",
        data: {
          ...authData,
          logInOperation: 0,
        },
      });
      refreshAuthToken();
    }
  }, [sendData.url, authData.logInOperation, refreshStatus.current]);

  //   returning sendData because some values if we dont want to change then use spread and resetting response so response once used we can set again
  return [sendData, setSendData, status, response, setResponse];
}

export default useFetch;
